<template>
  <div class="main">
    <h4>说明：该实验假定所有进程都在0时刻提交（就绪）😏</h4>
    <HPF class="mg"></HPF>
    <FCFS class="mg"></FCFS>
    <RR class="mg"></RR>
  </div>
</template>
<script>
// 先import组件，再把组件暴露出去
import HPF from '@/components/e1_process/HPF'
import FCFS from '@/components/e1_process/FCFS'
import RR from '@/components/e1_process/RR'
export default {
  components: {
    HPF,
    FCFS,
    RR
  }
}
</script>
<style lang="scss" scoped>
  .main {
    padding: 10px 50px;
  }
  .mg {
    margin: 100px 0;
  }
</style>



